import { Component, OnInit} from '@angular/core';
import { HttpService } from './http.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit{
  title = 'Favorite Authors';
  authors:any
  author:any
  newAuthor:any
  updateAuthor:any
  errorName:string
  errorQuote:any
  constructor(private _httpService: HttpService){
    this.authors = []
    this.author = {name: ''}
  }

  ngOnInit(){
    this.getAuthorsFromService()
  }

  getAuthorsFromService(){
    this._httpService.getAuthors()
    .subscribe(data => {
      console.log('Got our authors', data)
      this.authors = data;
      console.log(this.authors)
    })
  }

  getIdAuthor(author){
    this.authors = author
    console.log(this.author)
  }

  onNewAuthor(){
    this._httpService.addAuthor(this.newAuthor)
    .subscribe((data:any) =>{
      console.log('Got data from post back', data);
      this.newAuthor = {name: '', quote: ''}
      if(data.errors && data.errors.name){
        this.errorName = data.errors.name.message;
      }
      if(data.errors && data.errors.quote){
        this.errorQuote = data.errors.quote.message;
      }
      console.log(this.newAuthor)
      this.getAuthorsFromService()
    })
    console.log(this.newAuthor)
  }

  onEditSubmit(){
    this._httpService.updateAuthor(this.updateAuthor)
    .subscribe(data => {
      console.log('Got data from post back', data);
      console.log(this.updateAuthor)
      this.getAuthorsFromService()
    })
  }

  onDeleteClick(author) {
    this._httpService.deleteAuthor(author)
    .subscribe(data => {
      console.log('Delete Data', data);
      this.getAuthorsFromService()
    })
  }

}
