import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { HttpService } from '../http.service';
@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit { 
  @Input() a:any
  author:any
  updateAuthor:any
  errorName:string
  errors:string
  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _httpService: HttpService
  ) {
    this.author = {name: ''}
    this.updateAuthor = {name: ''}
  }

  ngOnInit() {
    // this._route.params.subscribe((params: Params) => {
    //   console.log(params['id'])
    // })
    // this.updateAuthor = {_id:this.a._id, name: this.a.name}
    this.authorToEdit()
  }
  goHome() {
    this._router.navigate(['/home']);
  }
  onEditSubmit(){
    this._httpService.updateAuthor(this.updateAuthor)
    .subscribe((data:any) => {
      console.log('Got data from post back', data);
      console.log(this.updateAuthor)
      if(data.errors && data.errors.name){
        this.errorName = data.errors.name.message;
      }
      else{
        this._router.navigate(['/'])
      }
    })
  }
  authorToEdit(){
    this._route.params.subscribe((params: Params) =>{
      this._httpService.getAuthorId(params['id'])
      .subscribe(data => {
        this.author = data
        this.updateAuthor = this.author
      })
    })
  }
}
